.foldable {
  & > .foldable--title {
    cursor: pointer;
    margin-bottom: 0;
  }
  & > .foldable--title:before {
    font-family: 'Material Icons';
    content: 'chevron_right';
    font-feature-settings: 'liga' 1;
    font-size: 3rem;
    position: relative;
    top: 14px;
  }
  & > .foldable--body {
    max-height: 0;
    overflow: hidden;
    transition: max-height 300ms cubic-bezier(0, 1, 0, 1);
  }
}

.foldable__unfolded {
  & > .foldable--title:before {
    content: 'expand_more';
  }

  & > .foldable--body {
    max-height: 500px;
    transition: max-height 300ms ease-out;
  }
}
